(function() {
  'use strict';

  angular
    .module('financeWebapp')
    .controller('SignupController', SignupController);
  /** @ngInject */
  function SignupController(toastr, $timeout, $http, $log) {
    var vm = this;
    vm.message = 'Join Us';
    vm.newUser = {};
    vm.newUserRequestSent = false;
    vm.showNewUserForm = true;
    vm.showNewUserInstructions = false;
    vm.CheckPassword = function(password, confirmPassword)
    {
        if(password == confirmPassword)
        {
            vm.passwordEquals = true;
            vm.passwordClass = "passwordMatch";
        }
        else
        {
            vm.passwordEquals = false;
            vm.passwordClass = "";
        }
    }

    vm.showToastr = function () {
      vm.message = 'Thank you!';
      vm.newUserRequestSent = true;
        $http({
        method: 'POST',
        url: 'https://www.itoushi.jp/api_signup.php',
        headers: {'Content-Type': 'application/x-www-form-urlencoded'},
        transformRequest: function(obj) {
            var str = [];
            for(var p in obj)
            str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
            return str.join("&");
        },
        data: {
          "userfullName" : vm.newUser.fullName,
          "userName": vm.newUser.userName,
          "userEmail": vm.newUser.email,
          "userPassword": vm.newUser.password
        }
      }).then(function successCallback(response) {
          toastr.info('Your request was succefully sent!');
          $timeout(function(){ vm.showNewUserForm = false }, 700); 
          $timeout(function(){ vm.showNewUserInstructions = true }, 1500);
          $log.info(response)
        }, function errorCallback(response) {
          $log.info(response)          
          // called asynchronously if an error occurs
          // or server returns response with an error status.
        }); 
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('financeWebapp')
    .controller('NewsfeedController', NewsfeedController);

  /** @ngInject */
  function NewsfeedController() {
    var vm = this;
    vm.postsId = 4;
    vm.share = function() 
    {
      if(vm.postMessage)
      {
        vm.posts.push({
            id: vm.postsId,
            userAvatar: "assets/images/person64.png",
            message: vm.postMessage,
            indicator: vm.indicator,
            imageType: vm.base64pic.filetype,
            imageBase64: vm.base64pic.base64
        });
        vm.postsId++;   
        vm.postMessage = '';
        vm.indicator = '';
        vm.base64pic = {}; 
      }
    }
    vm.posts = [{
     id:1,
     userAvatar:"assets/images/user1.png",
     message:"Lorem ipsum dolor sit amet, consectetur adipisicing elit. Fugit itaque consectetur repellat autem, dolorum sit voluptate laboriosam natus dicta provident!",
     indicator:"Bullish"
    },{
      id:2,
      userAvatar:"assets/images/user2.png",
      message:"U.K. Prime Minister Theresa May's future as Britain's top politician was in doubt Friday after polls suggest her decision to call a snap election has narrowed the already-slim majority for her ruling Conservative party",
      image:"assets/images/TweetImages/stream_85440989-.png",
      indicator:"Bearish"
    },{
      id:3,
      userAvatar:"assets/images/user3.png",
      message:"German Dep ForMin: Canâ€™t Waste Any Time Negotiating Brexit - RTRS",
      image:"assets/images/TweetImages/stream_85461306-.jpg",
      indicator:"Bearish"
    }];
  }
})();

(function() {
  'use strict';

  angular
    .module('financeWebapp')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($auth,toastr) {
    var vm = this;

    vm.authenticate = function(provider) {
      $auth.authenticate(provider);
    };

    vm.login = function()
    {
      toastr.info('Sample login request should be sent to the server now...');
    }
  }
})();

